<script>

import KetThuc from '../components/KetThuc.vue'
import Modau from '../components/Modau.vue'
import {Clicks} from '../data/Click' 
import LeftCom from '../components/Lefts.vue'
import RightCom from '../components/Right.vue'
export default {
  name: 'su-tich-cu-kieu',
  data: () => ({
    anh: 'https://cotich.net/uploads/news/a_284932446_su-tich-cu-kieu.jpg',
    per: -1,
    next: -1,
    ht: -1,
    ht2: -1,
    arrClassScc: ["", "", "", "", "", "", "", "", "", "", ""],
    kkk: 1
  }),
  metaInfo: {
    title: 'Sự tích củ kiệu',
    meta: [
      { name: 'thumbnail', content: 'https://cotich.net/uploads/news/a_284932446_su-tich-cu-kieu.jpg' },
      { charset: 'utf-8' },
      { name: 'Keywords', content: 'Sự tích củ kiệu' },
      { name: 'Description', content: 'Công chúa Kiệu vốn yêu thích trồng trọt đã phát hiện ra một cây cỏ lạ. Sau đó đem về trồng thì nhận được một loại củ có vị nồng gắt. Tuy nhiên, sau khi ngâm với giấm và nước ngọt của cây thì lại có hương vị nồng mới lạ. Kết hợp cùng thịt hoặc bánh chưng thì thơm ngon tuyệt vời. Chính vì vậy, sau khi nhà vua ăn thử đã đặt tên cho loại củ này là củ Kiệu để ghi nhớ công khám phá. ' },

      { itemprop: 'name', content: 'Truyện cổ tích' },
      { name: 'viewport', content: 'width=device-width, initial-scale=1, maximum-scale=1' },
      { name: 'language', content: 'Vietnamese,vn' },
      { name: 'geo.region', content: 'VN' },
      { property: 'article:tag', content: 'Truyện cổ tích, Chuyện cổ tích' },
      { name: 'robots', content: 'index,follow' },
      { name: '', content: '' },
    ]
  },
  components: {
    LeftCom,
    RightCom,
    Modau,
    KetThuc
  },
  methods: {
     denmodau(){
      Clicks.clickmodau();
    },
    denketthuc(){
      Clicks.clickketthuc();
    },
    clicks(vts) {
      this.ht = vts + 1;
      this.ht2 = vts;
      this.per = vts - 1;
      this.next = vts + 1;
      if (this.ht % 2 === 0) {
        this.arrClassScc[this.ht2] = this.setClass(this.arrClassScc[this.ht2], false);
        if (this.per > 0) {
          this.arrClassScc[this.per] = this.setClass(this.arrClassScc[this.per], false);
        }
      } else {
        this.arrClassScc[this.ht2] = this.setClass(this.arrClassScc[this.ht2], true);
        if (this.next < this.arrClassScc.length) {
          this.arrClassScc[this.next] = this.setClass(this.arrClassScc[this.next], true);
        }
      }
      this.kkk++;
    },
    setClass(sClass, add) {
      var f = 'flipped';
      if (add) {
        if (sClass.indexOf(f) < 0) {
          sClass = sClass + " " + f;
        }
      } else {
        if (sClass.indexOf(" " + f) > 0) {
          sClass = sClass.replace(" " + f, "");
        } else
          if (sClass.indexOf(f) > 0) {
            sClass = sClass.replace(f, "");
          }
      }
      return sClass;
    },
    getCl(vt) {
      return this.arrClassScc[vt];

    },
    getZindex(vt) {
      if (vt % 2 === 0) {
        return "z-index: " + (4 - vt) + ";"
      } else {
        return "";
      }
    }
  },
  created() {
  }
}
</script>

